const ThongDiepContent = () => (
  <section id="thong-diep">
    <div className={"page-title"}>
      <h1>TÀI LIỆU THỰC HÀNH DƯỠNG SINH</h1>

      <h1>(PHẦN CĂN BẢN)</h1>
      <p>THÔNG ĐIỆP CỦA SỰ SỐNG</p>
    </div>

    
    <h1>PHẦN 1: LÝ THUYẾT</h1>

    <p>
      <em>
        (Người tập dưỡng sinh cần đọc kỹ phần lý thuyết này trước khi tiến hành
        thực nghiệm, vì đây là chìa khóa mở ra tất cả các vấn đề liên quan đến
        “Con đường dưỡng sinh” )
      </em>
    </p>
    <h2>DƯỠNG SINH VỚI BẢN CHẤT SỰ SỐNG</h2>

    <h3>Dưỡng sinh:</h3>

    <p>
      Nguồn gốc của dưỡng sinh đã có từ rất xa xưa, ít ra là từ thời thượng cổ (
      đã được nói đến trong Nội Kinh, một bộ sách kinh điển hàng đầu của y học
      Phương Đông, ra đời cách đây 5000 năm). Người xưa đã xem Dưỡng sinh là Đạo
      Sống* mà ai ai cũng thực hành (tuy nhiên, về sau ý nghĩa ấy dần dần mất đi
      mà chỉ còn là thuật dưỡng sinh mang tính cá nhân hạn hẹp).
    </p>
    <p>
      Với thời đại ngày nay, khi môi trường sống bị hủy hoại và xuống cấp nghiêm
      trọng (cả môi trường xã hội và môi trường tự nhiên), việc tái thiết lập
      một môi trường sống lành mạnh trở nên cấp thiết hơn bao giờ hết, thì Dưỡng
      sinh là Đạo sống vô cùng thiết thực. Vậy Dưỡng sinh là gì?
    </p>
    <p>Trước nhất, ta cần tìm hiểu ý nghĩa của từ Dưỡng sinh:</p>
    <p>Dưỡng: nuôi dưỡng, bảo dưỡng</p>
    <p>Sinh: Sự sống</p>
    <p>
      Vậy Dưỡng sinh là nuôi dưỡng, bảo dưỡng sự sống; là tạo mọi điều kiện
      thuận lợi cho sự Sống được thể hiện một cách trọn vẹn, hoàn hảo đúng với
      bản chất của sự Sống. Vậy Sự sống được hiểu như thế nào?
    </p>
    <p>
      Sự sống luôn bao gồm người sống và môi trường sống, là hai yếu tố gắn bó
      mật thiết tương quan, tác động lẫn nhau không thể tách rời; trong đó,
      <strong>người sống đóng vai trò trung tâm mang tính quyết định</strong>.
    </p>
    <p>
      Đối với môi trường sống có thể chia thành môi trường thiên nhiên (MTTN) và
      môi trường xã hội (MTXH)
    </p>
    <p>
      Về MTTN, nếu không có sự can thiệp quá đáng của con người thì tự nó luôn
      luôn vận hành theo đúng quy luật tự nhiên, hoàn toàn phù hợp với sự tồn
      tại và phát triển của mọi sinh vật trên trái đất; không những thế nó còn
      là người bạn đồng hành thân thiết, là tổng phổ nghệ thuật tuyệt vời luôn
      gợi cảm hứng cho con người.
    </p>
    <p>
      Vấn đề ta cần quan tâm ở đây chính là MTXH, vì nó tác động trực tiếp đến
      chất lượng sống của con người trong xã hội, cũng như ảnh hưởng rất lớn đến
      MTTN.
    </p>
    <p>
      Môi trường xã hội là do những người sống trong xã hội tạo nên; nói rõ hơn
      là do nhận thức, quan niệm, tư tưởng, định kiến, niềm tin và do những quy
      định, quy ước… của những người trong xã hội tạo ra. Khi những điều này phù
      hợp với quy luật và bản chất của sự sống sẽ tạo, ra MTXH lành mạnh, tốt
      đẹp; những người sống trong môi trường tinh thần và thể chất tạo ra một
      đời sống an vui, hạnh phúc. Ngược lại nếu không phù hợp( lệch lạc, mê tín)
      sẽ tạo ra một đời sống bất an, bất ổn, dễ tổn thương và đau khổ ( cả thân
      và tâm).
    </p>
    <p>
      Vì thế, muốn có một cuộc sống hạnh phúc thật sự, cần có sự chuyển biến về
      tư tưởng, quan niệm, nhận thức, và niềm tin trong xã hội; mà xã hội lại do
      các cá nhân hợp thành, nên trách nhiệm lại quay về mỗi cá nhân trong xã
      hội. Mỗi cá nhân cần có nhận thức, suy nghĩ, lời nói và hành động đúng
      đắn, phù hợp với bản chất và quy luật của Sự sống; người sống hợp với bản
      chất và quy luật của Sự sống sẽ được nuôi dưỡng, bảo hộ bởi môi trường
      năng lượng sống, nên sông trong an bình, hòa hợp, hạnh phúc.
    </p>
    <p>
      Tóm lại,
      <strong>
        Dưỡng sinh là tạo ra một lối sống, một cách sống, một thái độ sống phù
        hợp với bản chất và quy luật của Sự sống
      </strong>
      . Trước hết ta cần tìm hiểu bản chất của sự sống là gì.
    </p>
    <p>2. Bản chất Sự sống:</p>
    <p>
      Từ xưa đến nay, các nhà khoa học đã nghiên cứu rất nhiều lĩnh vực khác
      nhau liên quan đến Sự sống với những sáng kiến phát minh thật kỳ diệu, thú
      vị; nhưng thật ra chủ yếu chỉ tập trung vào các sự vật, hiện tượng bên
      ngoài chứ chưa chạm đến bản chất đích thực của Sự sống, chưa làm rõ mối
      tương quan giữa con người với Sự sống, cũng như chưa nói lên được vị trí
      vai trò của con người trong thế giới vũ trụ.
    </p>
    <p>
      <strong>Vậy bản chất đích thực của sự sống là gì?</strong>
    </p>
    <p>
      Như đã phân tích trên: Sự sống luôn bao gồm người sống và môi trường sống;
      môi trường sống luôn hàm chứa MTTN và MTXH ( MTXH đã được phân tích ở phần
      trước). Về MTTN thì rất rộng lớn, gồm thâu toàn bộ vũ trụ và thế giới động
      thực vật ( đất, nước, không khí, ánh sáng, các tia vũ trụ…).
    </p>
    <p>
      <strong>
        Như vậy sự sống là Tổng thể thống nhất bao quát toàn bộ tự nhiên, con
        người, xã hội; không có gì nằm ngoài Sự sống.
      </strong>
    </p>
    <p>
      Nói một cách khác, Tổng thể hay Toàn thể (tự nhiên, con người, xã hội) là
      một cơ cấu giống như một cơ thể (vĩ đại); và mỗi người là một phần là một
      phần tử hay là một tế bào của “cái cơ thể” ấy, giống như một tế bào trong
      cơ thể người ( tức là mỗi người đều chứa toàn bộ thông tin về Tổng thể).
    </p>
    <p>
      Nếu nhận thức một cách sâu sắc về vấn đề nếu trên thì con người không bao
      giờ làm tổn thương lẫn nhau dù bất cứ lý do gì, cũng không thể làm tổn hại
      đến môi trường sống nói chung (vì như thế chẳng khác gì tự làm hại mình).
    </p>
    <p>
      Như đã phân tích ở phần trước, MTXH có ảnh hưởng rất lớn đến chất lượng
      sống của mỗi con người; trong đó, quan hệ ứng xử giữa người và người trong
      xã hội là vấn đề trọng tâm cốt yếu cần giải quyết, nhằm tạo ra một môi
      trường tốt đẹp. Với vấn đề này, có thể nói rằng tổ tiên dân tộc Việt Nam
      đã có một tầm nhìn rất sâu xa, nên mới để lại một thông điệp tuyệt vời
      mang ý nghĩa hết sức sâu sắc về mối quan hệ gắn bó, tương thân tương ái
      giữa người và người trong cộng đồng xã hội, mà điển hình nhất là câu tục
      ngữ quen thuộc: “Thương người như thể thương thân” và hai tiếng “Đồng bào”
      thiêng liêng.
    </p>
    <p>
      <strong>
        Đây chính là cốt lõi của đạo Dưỡng sinh, là chân lý, quy luật sống cho
        muôn đời.
      </strong>
    </p>
    <p>
      Tóm lại, hễ có Sự sống, ắt cần phải có dưỡng sinh (Người sống thì phải
      biết Dưỡng sinh và cần có trách nhiệm với đạo Dưỡng sinh); một điều hết
      sức tự nhiên, tất yếu, hợp quy luật. Cho nên, Dưỡng sinh quả là Đạo sống
      không thể thiếu được. Đó là sứ mệnh, là nguồn sống, nguồn hạnh phúc của
      mỗi con người; đồng thời cũng chính là nền tảng vững chắc cho một xã hội
      tốt đẹp lý tưởng mà con người hằng mơ ước.
    </p>
    <h2>
      <strong>CỘI NGUỒN VÀ NĂNG LƯỢNG TỔNG THỂ</strong>
    </h2>

    <p>(Nguyên lý căn bản tổng quát)</p>
    <p>
      Có một nền tảng chung cho con người và cho tất cả mọi vấn đề, mọi sự vật
      hiện tượng trên thế giới (bao gồm cả vũ trụ); có thể gọi nền tảng chung
      tổng quát này là CỘI NGUỒN.
    </p>
    <p>
      Từ cội nguồn này hình thành một trường năng lượng Tổng thể thống nhất
      (toàn vẹn, đầy đủ). Từ đấy mới sinh ra con người, thế giới vũ trụ cũng như
      tất cả mọi sự vật, hiện tượng ở trong đó.
    </p>
    <p>
      Năng lượng Tổng thể hay năng lượng Cội nguồn là năng lượng sống có trí
      tuệ, vô cùng tinh tế và hoàn hảo nên không có bất kỳ một loại máy móc công
      cụ nào có thể phát hiện được đầy đủ, mà chỉ có cơ thể người sống mới có
      khả năng phát hiện được trọn vẹn nguồn năng lượng này và phát huy hết năng
      lực của nó (vì bộ máy cơ thể người cũng từ đấy mà ra, khả năng này được
      ghi lại trong các tế bào của cơ thể người, và di truyền từ đời này sang
      đời khác và không bao giờ mất đi).
    </p>
    <p>
      Dựa vào nguồn năng lượng này, con người hoàn toàn có thể bước vào dòng
      chảy của Sự sống (bước đúng nhịp điệu, đúng quy luật); từ đó nhận thức
      được bản chất và quy luật của Sự sống, sống hòa hợp với bản chất ấy, nên
      ngày càng thể hiện đầy đủ phẩm chất hết sức tốt đẹp của Sự sống (hoàn
      thiện về thể chất, tâm hồn và trí tuệ).
    </p>
    <p>
      Trí tuệ và tình thương phát huy cao độ, nhận thức ngày càng sâu sắc giúp
      con người ngày càng tự chủ hơn trong cuộc sống; tức là
      <strong>
        tự làm chủ về sức khỏe, cảm xúc, tư tưởng…; giải phóng con người khỏi
        mọi tư tưởng, quan niệm lệch lạc, mê tín;{" "}
      </strong>
      từ đó tạo ra một MTXH lành mạnh và MTTN cũng được bảo dưỡng tốt nhất trở
      lại là người bạn thân thiết của con người, làm cho đời sống con người ngày
      càng hoàn thiện hơn.
    </p>
    <p>
      Như vậy hướng về cội nguồn, dựa vào năng lượng Tổng thể là con đường (đơn
      giản và dễ dàng nhất) giúp cho tất cả mọi người, bất cứ ai cũng gắn kết
      lại với nguồn cội thiêng liêng siêu việt của mình; và nguồn năng lượng vĩ
      đại này sẽ nâng đỡ, dẫn dắt bản thân mình đi vào con đường tự hoàn thiện
      về tâm hồn, trí tuệ cũng như thể chất, giúp cho cuộc sống của mỗi người
      ngày càng phù hợp với quy luật và hòa hợp với Tổng thể; từ đó chứng nghiệm
      được bản chất đích thực của Sự sống, sống trong bản chất ấy, bộc lộ những
      phẩm chất tốt đẹp cùng những năng lực tuyệt vời còn tiềm ẩn trong mỗi con
      người, góp phần tạo nên đời sống xã hội tốt đẹp, kiến tạo một thế giới
      thái bình, an vui, hạnh phúc.
    </p>
    <h1>PHẦN 2: THỰC NGHIỆM</h1>

    <h3>CÁCH TIẾN HÀNH:</h3>

    <p>
      Ban đầu nên chọn một tư thế thoải mái phù hợp với mình (ngồi, nằm hoặc
      đứng); sau khi ổn định tư thế, cần thả lỏng toàn thân, chú ý lắng nghe cơ
      thể mình; để tăng sức chú ý, nên nhắm mắt lại. Mọi việc diễn ra sau đó cứ
      để tự nhiên, không dùng ý niệm suy xét, phán đoán hay can thiệp, chỉ giữ
      trạng thái (thái độ) quan sát, lắng nghe một cách khách quan, vô tư.
    </p>
    <p>
      Sau một thời gian thực nghiệm, khi người tập đã thuần thục có thể tiến
      hành mọi nơi mọi lúc, tùy điều kiện mỗi cá nhân (lúc ngồi xe, tàu, dã
      ngoại, giải lao, chờ xe…). Dần dần việc thực nghiệm sẽ trở nên gắn bó mật
      thiết với cuộc sống của mình. (Ngay trong những hoạt động thường ngày)
    </p>
    <h3>
      PHÂN TÍCH QUÁ TRÌNH TRẢI NGHIỆM TÁC ĐỘNG CỦA NĂNG LƯỢNG CỘI NGUỒN(CÒN GỌI
      LÀ KẾT NỐI VỚI CỘI NGUỒN)
    </h3>

    <p>
      Như phần nguyên lý đã chỉ rõ: Năng lực phát hiện ra trường năng lượng Cội
      Nguồn đã có sẵn trong mỗi tế bào của chúng ta; cho nên ta chỉ cần thư
      giãn(thả lỏng) cơ thể và lắng nghe cơ thể mình thì sẽ cảm nhận nguồn năng
      lượng này, thông qua tác động của nó lên các cơ quan bộ phận cùng hệ thần
      kinh, tạo ra các phản ứng khác nhau:
    </p>
    <ul>
      <li>
        Các cảm giác: kiến bò, nhột, tê, ngứa, nóng lạnh, nặng, nhẹ, căng, tức,
        nhói, đau, châm chích, thậm chí bay bổng, tan biến…
      </li>

      <li>
        Các âm thanh, ánh sáng, màu sắc, mùi, vị, hoặc như đang xem phim hay
        đang mơ…
      </li>

      <li>
        Các vận động: rung động, co giật, múa may, đi lại, thậm chí cười, khóc,
        hát, nói...
      </li>
    </ul>
    <p>
      Tất cả đều là những hiện tượng bình thường cho thấy rằng trường năng lượng
      Cội Nguồn đang hoạt động rà soát, điều chỉnh để đưa cơ thể (thân và tâm)
      trở về trạng thái hòa hợp thống nhất, cân bằng ổn định (lành mạnh).
    </p>
    <p>Có thể chia các trải nghiệm thành hai dạng là động và tĩnh:</p>
    <ol>
      <li>
        <strong>Trải nghiệm động: </strong>Là trạng thái thân vận động trong lúc
        kết nối như rung động múa may, đi lại, cười, khóc…
      </li>

      <li>
        <strong>Trải nghiệm tĩnh: </strong>Là trạng thái thân tĩnh tại trong lúc
        kết nối nhưng vẫn cảm nhận được sự hoạt động của năng lượng Cội nguồn
        thông qua các cảm giác trên thân thể và tâm trí (như đã nói ở phần
        trên).
      </li>
    </ol>
    <p>
      Tuy nhiên, cũng có những trường hợp người tập trong thời gian đầu không
      thấy hiện tượng gì rõ rệt, nhưng tâm trí vẫn tỉnh thức, sáng suốt thì vẫn
      rất tốt và sức khỏe được cải thiện.
    </p>
    <p>
      Ban đầu, ta không cần quan tâm đến trạng thái trải nghiệm động hay tĩnh,
      mà hãy để mọi việc diễn ra một cách tự nhiên tùy theo cơ địa và tâm lý của
      mỗi người. Mỗi dạng trải nghiệm có những đặc tính riêng như sau:
    </p>
    <ul>
      <li>
        Dạng động: Giúp cho những người có tâm trí thường phân tán, hay suy nghĩ
        lan man dễ dàng tập trung ổn định tâm trí và làm giảm cảm giác đau
      </li>

      <li>
        Dạng tĩnh: Mang tính sâu sắc hơn giúp cảm nhận rõ nguồn năng lượng đang
        vận hành trong cơ thể (cảm giác rõ hơn, mạnh hơn, nhất là những nơi có
        bệnh).
      </li>
    </ul>
    <p>
      Sau một thời gian luyện tập thì cơ thể dần dần đi đến chỗ ổn định, và ta
      hoàn toàn làm chủ trong các dạng trải nghiệm. Lúc này nên đưa tinh thần
      thực nghiệm vào trong đời sống hằng ngày để nhận ra cuộc sống chính là
      trải nghiệm tác động của năng lượng (vì tất cả vốn là năng lượng), cho đến
      khi ta cảm nghiệm được sự nâng đỡ, hướng dẫn, hỗ trợ thật sự của Tổng Thể.
      Đây là bước tiến đáng mừng chứng tỏ ta đã thật sự gắn kết chặt chẽ với
      nguồn cội của mình và cuộc sống của ta sẽ nhẹ nhàng hơn rất nhiều vì ta đã
      bước thuận theo dòng chảy của Sự sống (thuận quy luật)
    </p>
    <h1>CHÚ GIẢI CÁC HIỆN TƯỢNG XẢY RA TRONG QUÁ TRÌNH THỰC NGHIỆM</h1>

    <p>
      Sỡ dĩ có các hiện tượng nêu trên là do cảm xúc bị đè nén không được bộc
      lộ; lòng mong cầu, ham muốn, ưa thích bị kiềm chế không thỏa mãn; những
      khúc mắc trong tâm tư tình cảm không được giải tỏa… nói chung tất cả những
      gì không được thông suốt trong tâm tư tình cảm đều để lại dấu ấn trong não
      bộ cũng như trên toàn cơ thể gây trở ngại, ách tắc, rối loạn cho sự vận
      hành của năng lượng trong cơ thể; hoặc những thông tin được thu nhận từ
      môi trường sống chưa được xử lý triệt để. Khi cơ thể, trí óc được thư giãn
      và được chú ý quan sát lắng nghe, thì sẽ xảy ra quá trình rà soát toàn bộ
      (giống như quá trình quét kiểm tra trong máy tính). Tất cả những gì ẩn kín
      trong tiềm thức sẽ bộc lộ ra một cách tự nhiên và được nhận diện xử lý một
      cách hài hòa, thú vị dưới tác động của năng lượng Tổng thể; ta chỉ cần để
      mọi việc diễn ra một cách tự nhiên; sau khi đi hết chu trình thì các hiện
      tượng vận động của cơ thể sẽ tự động dừng lại. Tuy nhiên, ta vẫn hoàn toàn
      chủ động trong việc để cho tiếp tục quá trình vận động hay dừng lại; nếu
      muốn dừng lại thì quyết định dứt khoát trong trí: “dừng lại” hoặc “thôi,
      nghỉ” thì sẽ được như ý.
    </p>
    <p>
      Giải thích trên đây cũng chính là sự khái quát cơ chế gây bệnh và quá
      trình tự điều chỉnh của cơ thể dưới tác động của năng lượng Cội nguồn nhằm
      đưa cơ thể về trạng thái lành mạnh.
    </p>
    <h1>PHẦN 3: KẾT LUẬN</h1>

    <p>
      Sự sống là một tổng thể (Tổng Thể) thống nhất không thể chia cắt. Mỗi
      người là một phần tử, một tế bào của Sự sống, ẩn chứa toàn bộ thông tin về
      Sự sống. Vì vậy, chú ý quan sát lắng nghe chính mình (cơ thể mình), cũng
      chính là từng bước đi sâu vào bản chất của Sự sống. Đây chính là lựa chọn
      đơn giản, thông minh, sáng suốt nhất mà mỗi người có thể làm để có một đời
      sống trọn vẹn, sâu sắc, ý nghĩa và hạnh phúc.
    </p>
    <h1>PHẦN 4: BỔ SUNG</h1>

    <h2>Chú thích về năng lượng Tổng thể</h2>

    <p>
      Năng lượng Tổng thể có thể phân tích thành 2 phần chính tương đối như sau
    </p>
    <ul>
      <li>
        Phần “thô, nặng, đục,” kết tụ thành thế giới vũ trụ vật chất cụ thể, dễ
        thấy, có thể đo lường được.
      </li>

      <li>
        Phần “tinh, nhẹ, trong” là năng lượng tinh thần hình thành đời sống và
        các năng lực tinh thần có tính trừu tượng, khó thấy, không thể đo lường
        được
      </li>
    </ul>
    <p>Con đường vận hành khái quát của Sự sống</p>
    <ul>
      <li>Khởi từ Cội nguồn: Nền tảng tổng quát của Sự sống.</li>

      <li>
        Qua năng lượng Cội nguồn: Là sức sống, năng lực sống, khả năng thể hiện
        của Sự sống.
      </li>

      <li>
        Đến con người, thế giới, sự vật hiện tượng: Là hình thức thể hiện của Sự
        sống.
      </li>
    </ul>
    <p>
      Sự sống thì bao hàm cả ba phần (ba giai đoạn) trên. Con người là tế bào
      của Sự sống.
    </p>
    <p>
      Thực ra năng lượng Cội nguồn luôn luôn có trong mỗi người và thường xuyên
      tác động đến cuộc sống của con người, nhưng ta thường lãng quên không để ý
      mà chỉ quan tâm đến các sự vật hiện tượng bên ngoài (bề nổi) và bị lôi
      cuốn bởi hình thức, diễn biến của sự vật hiện tượng, thậm chí bị chi phối
      hoàn toàn bởi dáng vẻ bề ngoài; do đó đánh mất sự tự chủ, sáng suốt vốn có
      làm cho nhận thức bị sai lệch, sa vào mê tín dị đoan và cuộc sống trở nên
      lỗi nhịp, chệch choạc, rối rắm, thậm chí mịt mù không lối thoát.
    </p>
    <p>
      Vì vậy, việc quay về gắn kết lại với Cội nguồn là con đường
      <strong>duy nhất </strong>để thay đổi tình thế. Dựa vào năng lực của Toàn
      thể (năng lượng Tổng thể), Ta sẽ dần dần lấy lại thế chủ động để có cuộc
      sống nhẹ nhàng, thoải mái, an vui.
    </p>
    <p>
      <em>Ngày 25 tháng 9 năm 2014</em>
    </p>
    <p>CHÂN NHÂN</p>
  </section>
);

